  padding: 5px;
}

.items {
  display: flex;
  justify-content: center;
  gap: 10px;
}
.item{
cursor: pointer;
}
.content {
  background-color: beige;
  margin-top: 5px;
  padding: 5px;
  border-radius: 5px;
}
